<template>
    <div id="app">
        <header>
            <div class="header-content">
                <img src="https://vuejs.org/images/logo.png" alt="Vue.js Logo" class="vue-logo" />
                <h1>Vue 2 Nepali Date Picker Demo</h1>
            </div>
            <div class="header-logos">
                <a href="https://github.com/reezayn/vue2-nepali-datepicker" target="_blank">
                    <img src="https://logos-world.net/wp-content/uploads/2020/11/GitHub-Symbol.png" alt="GitHub Logo" />
                </a>
                <a href="https://www.npmjs.com/package/vue2-nepali-datepicker" target="_blank">
                    <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/d/db/Npm-logo.svg/512px-Npm-logo.svg.png"
                        alt="npmjs Logo" />

                </a>
            </div>
        </header>
        <main>
            <section v-for="(example, index) in examples" :key="index" class="example-card">
                <h2>{{ example.title }}</h2>
                <p class="description">{{ example.description }}</p>
                <!-- Render the date picker component -->
                <div class="component-wrapper">
                    <component :is="example.component" v-bind="example.props" @date-selected="example.handler" />
                </div>
                <div class="code-section">
                    <div class="code-container">
                        <pre class="code-block">{{ example.code }}</pre>
                        <button class="copy-button" @click="copyCode(example.code, $event)">Copy Code</button>
                    </div>
                    <div class="output-container">
                        <h3>Output:</h3>
                        <pre class="output">{{ example.output }}</pre>
                    </div>
                </div>
            </section>
        </main>
        <footer>
            <p>
                Made by
                <a href="https://rijanshrestha.com.np" target="_blank">Rijan Shrestha</a>
            </p>
        </footer>
    </div>
</template>

<script>
// import { NepaliDatePicker } from '../../src/index.js';
import { NepaliDatePicker } from 'vue2-nepali-datepicker';

export default {
    name: 'App',
    components: { NepaliDatePicker },
    data() {
        return {
            outputFormatted: '',
            outputDetailed: '',
            outputCustom: '',
            outputDisplayFormat: '',
            outputRestricted: '',
            outputInitial: '',
            examples: []
        };
    },
    methods: {
        // Example handlers
        handleDateSelectedFormatted(date) {
            this.outputFormatted = date;
            console.log('Example 1:', date);
        },
        handleDateSelectedDetailed(date) {
            this.outputDetailed = JSON.stringify(date, null, 2);
            console.log('Example 2:', date);
        },
        handleDateSelectedCustom(date) {
            this.outputCustom = date;
            console.log('Example 3:', date);
        },
        handleDateSelectedDisplayFormat(date) {
            this.outputDisplayFormat = date;
            console.log('Example 4:', date);
        },
        handleDateSelectedRestricted(date) {
            this.outputRestricted = date;
            console.log('Example 5:', date);
        },
        handleDateSelectedInitial(date) {
            this.outputInitial = date;
            console.log('Example 6:', date);
        },
        // Custom return format function for Example 3.
        myCustomReturnFormat(year, month, day) {
            return `Nepali: ${day.n} of ${month}/${year}`;
        },
        // Clipboard copy function
        copyCode(code, event) {
            if (event && event.target) {
                const originalText = event.target.innerText;
                navigator.clipboard
                    .writeText(code)
                    .then(() => {
                        event.target.innerText = 'Copied';
                        setTimeout(() => {
                            event.target.innerText = originalText;
                        }, 5000);
                    })
                    .catch(() => {
                        event.target.innerText = 'Failed';
                        setTimeout(() => {
                            event.target.innerText = originalText;
                        }, 5000);
                    });
            }
        }
    },
    created() {
        // Build the examples array.
        this.examples = [
            {
                title: 'Example 1: Return Format "yyyy-mm-dd" (String)',
                description: 'Returns the selected date as a string in "yyyy-mm-dd" format (default display, English locale).',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: true,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Pick a Nepali Date',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: 'yyyy-mm-dd',
                    valueType: 'string'
                },
                handler: this.handleDateSelectedFormatted,
                code: `<NepaliDatePicker 
    :showEnglishDate="true" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Pick a Nepali Date"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    returnFormat="yyyy-mm-dd"
    valueType="string"
    @date-selected="handleDateSelectedFormatted" />`,
                output: this.outputFormatted
            },
            {
                title: 'Example 2: Return Format "detailed" (Object)',
                description: 'Returns the full day object for detailed processing.',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: true,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Pick a Nepali Date (Detailed)',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: 'detailed',
                    valueType: 'object'
                },
                handler: this.handleDateSelectedDetailed,
                code: `<NepaliDatePicker 
    :showEnglishDate="true" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Pick a Nepali Date (Detailed)"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    returnFormat="detailed"
    valueType="object"
    @date-selected="handleDateSelectedDetailed" />`,
                output: this.outputDetailed
            },
            {
                title: 'Example 3: Custom Return Format (Function)',
                description: 'Uses a custom function to return a formatted string.',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: true,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Pick a Nepali Date (Custom Format)',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: this.myCustomReturnFormat,
                    valueType: 'string'
                },
                handler: this.handleDateSelectedCustom,
                code: `<NepaliDatePicker 
    :showEnglishDate="true" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Pick a Nepali Date (Custom Format)"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    :returnFormat="myCustomReturnFormat"
    valueType="string"
    @date-selected="handleDateSelectedCustom" />`,
                output: this.outputCustom
            },
            {
                title: 'Example 4: Custom Display Format and Locale "np"',
                description: 'Uses a custom display format ("yyyy{sep}mm{sep}dd") with a custom separator. Weekdays appear in Nepali.',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: false,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Custom Display Format',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: 'yyyy-mm-dd',
                    displayFormat: 'yyyy{sep}mm{sep}dd',
                    customSeparator: '/',
                    locale: 'np',
                    valueType: 'string'
                },
                handler: this.handleDateSelectedDisplayFormat,
                code: `<NepaliDatePicker 
    :showEnglishDate="false" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Custom Display Format"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    returnFormat="yyyy-mm-dd"
    displayFormat="yyyy{sep}mm{sep}dd"
    customSeparator="/"
    locale="np"
    valueType="string"
    @date-selected="handleDateSelectedDisplayFormat" />`,
                output: this.outputDisplayFormat
            },
            {
                title: 'Example 5: Using minDate and maxDate (Restricted Range)',
                description: 'Restricts selectable dates between the specified minDate and maxDate.',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: true,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Restricted Date Range',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: 'yyyy-mm-dd',
                    valueType: 'string',
                    minDate: { nepaliYear: '2070', nepaliMonth: '1', day: 1 },
                    maxDate: { nepaliYear: '2090', nepaliMonth: '12', day: 31 }
                },
                handler: this.handleDateSelectedRestricted,
                code: `<NepaliDatePicker 
    :showEnglishDate="true" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Restricted Date Range"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    returnFormat="yyyy-mm-dd"
    valueType="string"
    :minDate="{ nepaliYear: '2070', nepaliMonth: '1', day: 1 }"
    :maxDate="{ nepaliYear: '2090', nepaliMonth: '12', day: 31 }"
    @date-selected="handleDateSelectedRestricted" />`,
                output: this.outputRestricted
            },
            {
                title: 'Example 6: Using Initial Date (Preselect and Initialize)',
                description: 'Initializes the date picker with a provided initial date string in the format "yyyy-mm-dd".',
                component: 'NepaliDatePicker',
                props: {
                    showEnglishDate: true,
                    primaryColor: '#60B7F0',
                    accentColor: '#60B7F0',
                    textColor: '#212121',
                    placeholder: 'Initial Date Example',
                    inputStyles: { width: '300px', border: '2px solid #ccc', borderRadius: '8px' },
                    returnFormat: 'yyyy-mm-dd',
                    valueType: 'string',
                    initialDate: '2085-10-25'
                },
                handler: this.handleDateSelectedInitial,
                code: `<NepaliDatePicker 
    :showEnglishDate="true" 
    primaryColor="#60B7F0" 
    accentColor="#60B7F0" 
    textColor="#212121"
    placeholder="Initial Date Example"
    :inputStyles="{ width: '300px', border: '2px solid #ccc', borderRadius: '8px' }"
    returnFormat="yyyy-mm-dd"
    valueType="string"
    :initialDate="'2085-10-25'"
    @date-selected="handleDateSelectedInitial" />`,
                output: this.outputInitial
            }
        ];

        // Watchers for live output updates.
        this.$watch('outputFormatted', (val) => { this.examples[0].output = val; });
        this.$watch('outputDetailed', (val) => { this.examples[1].output = val; });
        this.$watch('outputCustom', (val) => { this.examples[2].output = val; });
        this.$watch('outputDisplayFormat', (val) => { this.examples[3].output = val; });
        this.$watch('outputRestricted', (val) => { this.examples[4].output = val; });
        this.$watch('outputInitial', (val) => { this.examples[5].output = val; });
    }
};
</script>

<style scoped>
